export default {
	name: "listItem",
	filters: {
		km: function(val) {
			if (val != null) {
				return (Number(val) / 1000).toFixed(2) + 'km';
			} else {
				return "";
			}
		}
	},
	props: {
		mylist: {
			type: Array,
			default () {
				return []
			}
		},
		showkm:true
	},
	methods: {
		navigateTo(item) {
			this.$emit('navigateTo', item);
		}
	}
}
